"use client"

import {
  createContext,
  useContext,
  useEffect,
  useMemo,
  useState,
  type ReactNode,
} from "react"

const CART_STORAGE_KEY = "memoriza_cart_v1"

export interface CartItem {
  productId: string
  name: string
  imageUrl: string | null
  unitPrice: number
  promotionalPrice: number | null
  quantity: number
  // futuros campos opcionais:
  sizeId?: number
  sizeName?: string
  colorId?: number
  colorName?: string
  personalizationText?: string
}

interface CartContextValue {
  items: CartItem[]
  totalItems: number
  subtotal: number
  addItem: (item: CartItem) => void
  removeItem: (productId: string) => void
  clearCart: () => void
  updateQuantity: (productId: string, quantity: number) => void
}

const CartContext = createContext<CartContextValue | undefined>(undefined)

export function CartProvider({ children }: { children: ReactNode }) {
  const [items, setItems] = useState<CartItem[]>([])

  // Carregar do localStorage
  useEffect(() => {
    try {
      const raw = window.localStorage.getItem(CART_STORAGE_KEY)
      if (raw) {
        const parsed = JSON.parse(raw) as CartItem[]
        setItems(parsed)
      }
    } catch (err) {
      console.error("Erro ao carregar carrinho do localStorage:", err)
    }
  }, [])

  // Salvar no localStorage
  useEffect(() => {
    try {
      window.localStorage.setItem(CART_STORAGE_KEY, JSON.stringify(items))
    } catch (err) {
      console.error("Erro ao salvar carrinho no localStorage:", err)
    }
  }, [items])

  const addItem: CartContextValue["addItem"] = (item) => {
    setItems((prev) => {
      // mesma combinação → soma quantidade
      const index = prev.findIndex(
        (p) =>
          p.productId === item.productId &&
          p.sizeId === item.sizeId &&
          p.colorId === item.colorId &&
          p.personalizationText === item.personalizationText,
      )

      if (index === -1) {
        return [...prev, item]
      }

      const clone = [...prev]
      clone[index] = {
        ...clone[index],
        quantity: clone[index].quantity + item.quantity,
      }
      return clone
    })
  }

  const removeItem: CartContextValue["removeItem"] = (productId) => {
    setItems((prev) => prev.filter((p) => p.productId !== productId))
  }

  const clearCart: CartContextValue["clearCart"] = () => {
    setItems([])
  }

  const updateQuantity: CartContextValue["updateQuantity"] = (
    productId,
    quantity,
  ) => {
    if (quantity <= 0) {
      removeItem(productId)
      return
    }
    setItems((prev) =>
      prev.map((p) =>
        p.productId === productId ? { ...p, quantity } : p,
      ),
    )
  }

  const subtotal = useMemo(
    () =>
      items.reduce(
        (acc, item) =>
          acc +
          (item.promotionalPrice ?? item.unitPrice) * item.quantity,
        0,
      ),
    [items],
  )

  const totalItems = useMemo(
    () => items.reduce((acc, item) => acc + item.quantity, 0),
    [items],
  )

  const value: CartContextValue = {
    items,
    totalItems,
    subtotal,
    addItem,
    removeItem,
    clearCart,
    updateQuantity,
  }

  return <CartContext.Provider value={value}>{children}</CartContext.Provider>
}

export function useCart() {
  const ctx = useContext(CartContext)
  if (!ctx) {
    throw new Error("useCart must be used within a CartProvider")
  }
  return ctx
}